import React, { useEffect, useRef, useState } from 'react';
import { Chart, registerables } from 'chart.js';

const MetricsGraphs = ({ selectedArea, selectedSubArea, selectedYear }) => {
  const chartRef = useRef(null);
  const [chartInstance, setChartInstance] = useState(null);
  const [metricsData, setMetricsData] = useState([]);

  useEffect(() => {
    Chart.register(...registerables); // Register all chart.js components

    // Fetch metrics data from API
    const fetchMetricsData = async () => {
      const response = await fetch(`/api/metrics-data?area_id=${selectedArea}&subarea_id=${selectedSubArea}&year=${selectedYear}`);
      const data = await response.json();
      setMetricsData(data);
    };

    if (selectedArea && selectedSubArea && selectedYear) {
      fetchMetricsData();
    }
  }, [selectedArea, selectedSubArea, selectedYear]);

  useEffect(() => {
    if (chartRef.current && metricsData.length > 0) {
      const ctx = chartRef.current.getContext('2d');

      if (chartInstance) {
        chartInstance.destroy();  // Destroy previous chart instance
      }

      const labels = metricsData.map(item => `Interval ${item.interval_id}`);
      const data = metricsData.map(item => item.record_count);

      const newChartInstance = new Chart(ctx, {
        type: 'bar',
        data: {
          labels: labels,
          datasets: [{
            label: 'Number of Records',
            data: data,
            backgroundColor: 'rgba(75, 192, 192, 0.2)',
            borderColor: 'rgba(75, 192, 192, 1)',
            borderWidth: 1,
          }],
        },
        options: {
          scales: {
            y: {
              beginAtZero: true,
            },
          },
        },
      });

      setChartInstance(newChartInstance);  // Set the new chart instance
    }
  }, [metricsData]);  // Update chart when metrics data changes

  return (
    <div>
      <h3>Records by Interval</h3>
      <canvas ref={chartRef}></canvas>
    </div>
  );
};

export default MetricsGraphs;
